/*Stacked screen size for smaller devices*/

.layout {
  display: -ms-flexbox;
  display: flex;
  padding: 2em;
  -ms-flex-flow: column;
      flex-flow: column;
  height: 100%;
}

.appControls {
  -ms-flex: .5 1 0%;
      flex: .5 1 0%;
}

@media (min-width: 1440px) {
  .layout {
    -ms-flex-flow: row;
        flex-flow: row;
  }
}



